nav {
    display: flex;
    align-items: center;
    padding-left: 20px;
    padding-right: 50px;
    border-bottom: 2px solid grey;
    background-color:#F4F4F4;
  }
  
  .logo{
    border-radius: 50%;
  }

  .title {
    text-decoration: none;
    font-family: Futura, Arial, sans-serif;
    font-weight: bold;
    color: rgb(95, 156, 235);
  }
  
  .navTab {
    text-decoration: none;
    font-family: Futura, Arial, sans-serif;
    color: #000;
  }
  
  .login {
    text-decoration: none;
    margin-left: auto;
    border: 1px solid black;
    border-radius: 10px;
    font-family: Futura, Arial, sans-serif;
    color: #000;
    background-color: rgb(160, 211, 240);;
  }
  
  @media screen and (min-width: 400px) {
    .logo {
      width: 30px;
    }
    .title {
      font-size: 12px;
    }
    .navTab {
      margin-left: 20px;
      font-size: 15px;
    }
    .login {
      padding: 1px 5px;
      font-size: 12px;
    }
  }
  
  @media screen and (min-width: 600px) {
    .logo {
      width: 40px;
    }
    .title {
      font-size: 15px;
    }
    .navTab {
      margin-left: 20px;
      font-size: 15px;
    }
    .login {
      padding: 2px 10px;
      font-size: 15px;
    }
  }
  
  @media screen and (min-width: 900px) {
    .logo {
      width: 60px;
    }
    .title {
      font-size: 20px;
    }
    .navTab {
      margin-left: 50px;
      font-size: 18px;
    }
    .login {
      padding: 5px 20px;
      font-size: 18px;
    }
  }
  
  @media screen and (min-width: 1200px) {
    .logo {
      width: 80px;
    }
    .title {
      font-size: 25px;
    }
    .navTab {
      margin-left: 80px;
      font-size: 20px;
    }
    .login {
      padding: 5px 25px;
      font-size: 20px;
    }
  }
  